.popup__span-error {
  font-size: 12px;
  line-height: 15px;
  color: red;
  opacity: 0;
  margin-left: 36px;
  margin-right: 36px;
  height: 14px;
}

@media screen and (max-width: 375px) {
  .popup__span-error {
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 0;
    font-size: 10px;
  }
}

.popup__span-error_active {
  opacity: 1;
}
